import { Field } from 'mint-ui';
import { Picker } from 'mint-ui';
import addre from 'assets/address/addressinfo';
import cTitle from 'components/title';
import { MessageBox } from 'mint-ui';
const address = addre;
export default {
  data() {
    return {
      form: {
        username: '',
        mobile: '',
        city: '',
        address: '',
        province: '',
        isdefault: 0,
        district: '',
        uid: 9,
        isdefault: false

      },
      provinceoptions: [],
      cityoptions: [],
      regionoptions: [],
      provicevalue: '请选择省份',
      cityvalue: '请选择市区',
      regionvalue: '请选择辖区',
      popupVisible: false
    }
  },
  methods: {
    onAddressChange(picker, values) {
      picker.setSlotValues(1, address[values[0]]);
      this.addressProvince = values[0];
      this.addressCity = values[1];
      console.log(this.addressProvince, this.addressCity);
    },
    //选择省份的响应方法
    selectProviceHandle(value) {
      this.form.provicevalue = value.areaname;
      this.cityvalue = '请选择市区';
      this.regionvalue = '请选择辖区';
      this.cityoptions = [];
      for (let i = 0; i < this.$store.state.city.length; i++) {
        let item = this.$store.state.city[i];
        if (item.parentid == value.id) {
          this.cityoptions.push(item);
          // = this.cityoptions[0].areaname;
        }

      }

      //this.form.province = value;
    },
    //选择市的响应方法
    selectCityHandle(value) {
      console.log(value.areaname);
      this.form.city = value.areaname;
      this.regionoptions = [];
      for (let i = 0; i < this.$store.state.district.length; i++) {
        let item = this.$store.state.district[i];

        if (item.parentid == value.id) {
          this.regionoptions.push(item);
          //this.regionvalue = this.regionoptions[0].areaname;

        }

      }
    },
    //选择市辖区的响应方法
    selectRegionHandle(value) {
      console.log(value.areaname);
      this.form.district = value.areaname;;
    },
    appendAddress() {
      //console.log(this.form);
      var that = this;
      if (this.form.isdefault) {
        this.form.isdefault = 1;

      } else {
        this.form.isdefault = 0;
      }
      $http.get('member.member-address.store', { username: this.form.username, mobile: this.form.mobile, city: this.form.city, address: this.form.address, province: this.form.provicevalue, isdefault: this.form.isdefault, district: this.form.district }).then(function (response) {
        if (response.result == 1) {

          MessageBox('提示', response.msg);
          that.$router.go(-1);

        } else {
          MessageBox('提示', response.msg);
        }
      }, function (response) {
        // error callback
      });


    }
  },

  mounted() {
    this.provinceoptions = this.$store.state.province;


  },
  components: { cTitle }
};


